.comments {
  margin-top: 2rem;
  margin-left:0.5rem;
}

.comments-title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.comments-container {
  margin-top: 3rem;
}

.comment-form-title {
  font-size: 1.3rem;
}

.comment-form-textarea {
  width: 50%;
  height: 3rem;
  margin-bottom: 8px;
  margin-top: 8px;
  border: 1px solid rgb(107, 114, 12);
}

.comment-form-button {
  font-size: 16px;
  padding: 8px 16px;
  background: rgb(59, 130, 246);
  border-radius: 8px;
  color: white;
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: rgb(37, 99, 235);
}

.comment-form-button:disabled {
  opacity: 0.7;
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  margin-bottom: 28px;
}

.comment-image-container {
  margin-right: 12px;
}

.comment-image-container img {
  border-radius: 50px;
  max-height: 3rem;
}

.comment-right-part {
  width: 70%;
}

.comment-content {
  display: flex;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
}

.comment-text {
  font-size: 1.2rem;
  font-weight: bold;
}

.comment-actions {
  display: flex;
  font-size: 0.7rem;
  color: rgb(51, 51, 51);
  cursor: grab;
  margin-top: 0.6rem;
}

.comment-action {
  margin-right: 0.6rem;
}

.comment-action:hover {
  text-decoration: underline;
}

.replies {
  margin-top: 1.3rem;
}
